

.select {
  font-size: 1rem;
  font-weight: 600;
  padding: 10px;
  border: none;
  cursor: pointer;
  transition: all .3s;
}
.select:hover {
  opacity: .5;
}
.selectContainer {
  font-weight: 600;
  font-size: 1rem;
  line-height: 1rem;
  display: flex;
  gap: 10px;
  align-items: center;
}